.cat {
  gap: 0 30px;
  display: flex;
}

.cat-img {
  max-width: 50%;
  height: auto;
}

.cat-container {
  max-width: 600px;
  min-width: 250px;
}

.breed-select {
  margin-bottom: 20px;
  display: none;
}

.error {
  display: none;
}

.loader {
  height: 12px;
  width: 80%;
  border: 1px solid #fff;
  border-radius: 10px;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  height: 100%;
  width: 0;
  background: #ff3d00;
  animation: prog 6s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes prog {
  to {
    width: 100%;
  }
}

